@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  line-height: 28px;
  background-color: #f4f4f4;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.statistic-image {
  display: block;
}

.portfolio {
  margin-bottom: 50px;
}

.btn-up {
  position: fixed;
  bottom: 30px;
  right: 30px;
  padding: 20px;
  margin: 0;
  font-weight: bold;
  font-size: 20px;
  background-color: #90c5d8;
  border: none;
  border-radius: 5px;
  opacity: 0.4;
}

.btn-up:hover {
  opacity: 1;
}

.btn-up-visible {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  background: linear-gradient(to bottom right, #e60073 0%, #e51175 8.1%, #e0227b 15.5%, #da3184 22.5%, #d1408e 29%, #c64e99 35.3%, #b95ba5 41.2%, #ab67b0 47.1%, #9a71bb 52.9%, #887bc5 58.8%, #7584ce 64.7%, #608bd6 71%, #4a91dd 77.5%, #3395e2 84.5%, #1a98e5 91.9%, #0099e6 100%);
}

.header-wrapper {
  display: flex;
  gap: 15px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 5px;
  margin: 0 auto;
  min-height: 50px;
  width: 1280px;
}

@media (max-width: 768px) {
  .header-wrapper {
    width: 768px;
  }
}
@media (max-width: 480px) {
  .header-wrapper {
    width: 480px;
  }
}
.page-logo {
  font-family: "Roboto", sans-serif;
  color: #fff;
  font-size: 26px;
  font-weight: 900;
  padding-left: 70px;
  padding-right: 70px;
}

.header-title {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  margin: 0;
  padding: 0;
}

.header-link {
  color: #c3f7ef;
  cursor: pointer;
}

.navigation {
  position: sticky;
  top: 0;
  background-color: #a52e85;
  z-index: 10;
}

.navigation-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  list-style: none;
  margin: 0 auto;
}

.navigation-item {
  position: relative;
}

.navigation-link {
  font-size: 14px;
  line-height: 18px;
  color: #e3e3e3;
}

.navigation-link:hover {
  color: #ffffff;
  font-weight: 700;
}

.navigation-link--active::before:hover {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: #bee6f9;
  border-bottom: 5px solid #575fee;
}

.navigation-link--active {
  color: #fbe16d;
  font-weight: bold;
}

.navigation-link--active:hover {
  color: #fafafa;
  font-weight: bold;
}

.page-main {
  width: 1280px;
  margin: 0 auto;
  padding: 0 50px;
}

@media (max-width: 768px) {
  .page-main {
    width: 768px;
    padding: 30px 30px 0;
  }
}
@media (max-width: 480px) {
  .page-main {
    width: 480px;
    padding: 20px 20px 0;
  }
}
.intro-wrapper {
  position: relative;
  background-color: #4a38b4;
  background-image: url("../img/web-design.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  filter: drop-shadow(16px 16px 20px #acabab);
  border-radius: 7px;
  border-top-right-radius: 0;
}

.intro-wrapper::before {
  position: absolute;
  content: "Александр Страховенко - Веб-разработчик";
  top: 20px;
  left: 330px;
  color: aliceblue;
  font-weight: bold;
}

.subheading-blur {
  width: 450px;
  text-shadow: 0 0 5px #999;
  color: transparent;
  transition: all 0.5s;
}

.subheading-blur:hover {
  text-shadow: 0 0 0 #333;
}

.intro {
  margin-bottom: 50px;
}

.title {
  margin: 30px 0;
  padding: 0;
}

.page-main {
  width: 1280px;
  margin: 0 auto;
  padding: 0 50px;
}

@media (max-width: 768px) {
  .page-main {
    width: 768px;
    padding: 30px 30px 0;
  }
}
@media (max-width: 480px) {
  .page-main {
    width: 480px;
    padding: 20px 20px 0;
  }
}
.site-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  list-style: none;
}

.site-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 340px;
  padding: 10px;
  border: #dbdbde 1px solid;
  background-color: #fbfbfb;
}

.site-title {
  min-height: 60px;
  margin: 0;
}

.site-link {
  position: relative;
  display: block;
  min-height: 140px;
  text-align: center;
}

.site-description {
  min-height: 140px;
}

.site-image:hover {
  box-shadow: 9px 8px 8px -2px rgba(34, 60, 80, 0.21);
}

.site-info {
  position: absolute;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  bottom: 0;
  left: 0;
  color: #e40788;
  font-weight: normal;
  font-size: 16px;
  text-align: center;
  background-color: rgba(208, 208, 208, 0.5);
  transition: all 0.7s ease-in-out;
}

.site-info:hover {
  opacity: 1;
}

.skills-list {
  margin: 0;
  padding: 0;
}

.skills-list dt {
  padding-left: 32px;
  background-repeat: no-repeat;
  background-position: left;
}

.level {
  margin: 4px 0 16px;
  padding: 0;
  height: 14px;
  font-size: 0;
  background-color: #ffffff;
  border: 1px solid #86c3fa;
  border-radius: 8px;
}

.level div {
  margin: 1px;
  height: 12px;
  background-color: #288be6;
  border-radius: 6px;
}

.card-list {
  font-size: 14px;
  line-height: 19px;
  list-style: none;
  padding-left: 0;
}

.card-title {
  font-weight: 200;
}

.card-image {
  cursor: pointer;
}

.card-image:hover {
  box-shadow: 9px 8px 8px -2px rgba(34, 60, 80, 0.21);
}

.specialist-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 100px;
}

.specialist {
  width: 450px;
}

.specialist-image:hover {
  box-shadow: 9px 8px 8px -2px rgba(34, 60, 80, 0.21);
}

.alibra-wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 100px;
}

.alibra {
  width: 450px;
}

.alibra-title {
  min-height: 90px;
}

.alibra-image:hover {
  box-shadow: 9px 8px 8px -2px rgba(34, 60, 80, 0.21);
}

.other-projects {
  width: 780px;
}

.page-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
  color: #fff;
  font-weight: 700;
  min-height: 120px;
  padding: 10px;
  background: linear-gradient(228deg, #c33d9f 20%, #254891 100%, #000 100%);
  background: linear-gradient(228deg, #c33d9f 20%, #254891 100%, #000 100%);
}

.footer-link {
  color: #c3f7ef;
  cursor: pointer;
}

#myImg {
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

#myImg:hover {
  opacity: 0.7;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.7);
}

.modal-content {
  margin: auto;
  display: block;
}

#caption {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  height: 150px;
}

.close {
  position: absolute;
  top: 45px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}